import React, { Component } from "react";
import { Modal, ModalHeader, ModalBody, ModalFooter, Button } from "reactstrap";
import { connect } from "react-redux";

class ModalPost extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const { showdialog, onHandleDialog, DataSelect } = this.props;
    console.log(DataSelect);
    return (
      <div>
        <Modal isOpen={showdialog} toggle={() => onHandleDialog()}>
          <ModalHeader toggle={this.toggle}>{id}</ModalHeader>
          <ModalBody>
            <textarea
              style={{ width: "100%", height: "100px" }}
              value={this.state.data}
              onChange={e => this.onHandleTextarea(e)}
            />
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={()=>{}}>
              Save
            </Button>{" "}
            <Button color="secondary" onClick={()=>{}}>
              Cancel
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}
export default connect()(ModalPost);
